<template>
  <div class="row listing-wrapper bg-white mb-2">
    <div class="col-sm-6 fs-12">
      <div class="listing-title">{{ listing.membery.named }}</div>
      <div>{{ listing.membery.addr }}</div>
      <table>
        <tr>
          <td width="109px">客户编号</td> <td>{{ listing.membery.num }}</td> <td></td>
        </tr>
        <tr>
          <td>报价单号</td> <td>{{ listing.price_collect.num }}</td> <td></td>
        </tr>
      </table>
    </div>
    <div class="col-sm-6 text-right">
      <div class="listing-action">
        <button class="btn btn-outline-info btn-sm fs-12">打印</button>
        <button class="btn btn-outline-danger btn-sm fs-12" @click="itemOption(listing.id, 'trash')">删除</button>
      </div>
      <table class="listing-sub mt-2">
        <tr>
          <td></td><td width="120px">发票日期</td><td width="130px" class="text-right listing-sub-time">{{ listing.listing_time }}</td>
        </tr>
        <tr>
          <td colspan="3" class="text-right">
            <div class="listing-sub-icon float-right" @click="itemOption(listing.id, 'edit')">
              <i class="fa fa-angle-right"></i>
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'ListingTableItem',
  props: {
    listing: {
      type: Object
    }
  },
  methods: {

    itemOption(id, option) {
      if (option == 'trash') {
        $('#listingId').val(id)
        tool.showModal('trashModal')
      } 
      else if (option == 'update') {

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.listing-wrapper
  padding-top: $horizon
  padding-left: $horizon
  padding-right: 0px

.listing-title
  font-size: 18px

.listing-action
  padding-right: $horizon

.listing-sub
  width: 100%

.listing-sub-time
  padding-right: $horizon

.listing-sub-icon
  width: 120px
  padding-right: $horizon
</style>


