<template>
  <div>
    <listing-table-item v-for="i in listings" :key="i.id" :listing="i"></listing-table-item>

    <item-trash @trashItem="trashListing"></item-trash>
    <input type="hidden" value="" id="listingId">
  </div>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import ListingTableItem from './Inner/ListingTableItem.vue'
import ItemTrash from '@/components/Modal/Trash.vue'

export default {
  name: 'ListingLeft',
  components: {
    ListingTableItem, ItemTrash
  },
  data() {
    return {
      listings: service.listingList
    }
  },
  methods: {
    trashListing() {
      const id = $('#listingId').val()
      alert('删除 Id = ' + id)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


