<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.member-face
  width: 49px
  font-size: 38px
  padding: 0

.member-face .img
  background: #f6f6f6
  width: 49px
  height: 49px
  border-radius: 100%
  line-height: 1
  margin: 0 !important

</style>


