<template>
  <div>
    <div class="member-choise-panel">
      <div class="pl-2 mt-2">
        <listing-member-face-card :the_member="memberSelect"></listing-member-face-card>
      </div>
      <div class="fs-12 mt-4">选择客户：</div>
      <div class="filter-member-panel mt-1 pt-2">
        <listing-member-item v-for="m in members" :key="m.id" :select="(m.id == memberSelect.id)" @click="itemChoise(m.id)">
          <div slot="member_named">{{ m.named }}</div>
          <span slot="member_num">{{ m.num }}</span>
        </listing-member-item>
      </div>
    </div>
  </div>
</template>

<script>
import service from '@/service';
import ListingMemberItem from './Inner/ListingMemberItem.vue';
import ListingMemberFaceCard from '@/components/Card/ListingMemberFaceCard.vue';

export default {
  components: { ListingMemberItem, ListingMemberFaceCard },
  name: 'ListingRight',
  data() {
    return {
      memberSelect: service.members[0],
      members: service.members
    }
  },
  methods: {
    itemChoise(id) {
      alert('id = ' + id)
      const selected = this.members.filter((e) => { if(e.id == id) { return true } else { return false } })
      this.memberSelect = selected[0]
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.member-choise-panel
  padding: $horizonL

.filter-member-panel
  overflow: scroll
  height: calc( 48vh - 15px )


</style>


