<template>
  <div class="member-card-simple fs-12" :class="{ 'member-choise-active': select }">
    <div class="member-left-face float-left">
      <div class="face-img"></div>
    </div>
    <div class="member-right-msg float-left pl-2">
      <div class="">
        <slot name="member_named">--</slot>
      </div>
      <div class="fs-10">
        编号：<slot name="member_num">0000</slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListingMemberItem',
  props: {
    select: {
      type: Boolean
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.member-card-simple
  width: 100% !important
  padding: $distance $horizon
  padding-top: 5px
  margin-bottom: 3px

.member-left-face
  width: 30px
  height: 30px
  border-radius: 100%
  background: #f6f6f6

.face-select-active
  background: white

.member-choise-active
  background: #f7f7f7
  border-radius: $distance

.member-choise-active .member-left-face
  background: white

</style>


